<template>
	<v-row v-if="isSignin && user.UserRole.code == 3" dense>
		<v-col cols="12">
			<EMCOnSchoolSelector v-if="$route.params.subjectCode" />
		</v-col>
		<v-card
			v-if="emcsOnSchool.length === 0 && this.$route.params.schoolCode !== undefined"
			class="mx-auto text-center"
		>
			    
		</v-card>
		<v-col cols="12">
			<v-row v-for="emcOnSchool in emcsOnSchool" :key="emcOnSchool.id">
				<EMCOnSchoolCard :emc-on-school="emcOnSchool" @onDetachEMCFrom="detachEMCFrom" />
			</v-row>
		</v-col>
	</v-row>
</template>

<script>
import { mapFields } from 'vuex-map-fields'
import PooService from '../../services/pooService'
import EMCOnSchoolCard from './EMCOnSchoolCard.vue'
import EMCOnSchoolSelector from './EMCOnSchoolSelector.vue'

export default {
	components: {
		EMCOnSchoolCard,
		EMCOnSchoolSelector,
	},
	data: () => ({
		error: null,
	}),
	computed: {
		...mapFields(['emcsOnSchool', 'isSignin', 'user', 'emcs']),
	},
	watch: {
		$route() {
			this.getEMCsOnSchool()
		},
	},
	created() {
		this.$store.dispatch('setAreasSidebar', true) //  sidebar  EMCsOnSchool
		this.$store.dispatch('setSubjectsSidebar', false) //     sidebar EMCs  false
		this.getEMCsOnSchool()
	},
	methods: {
		async getEMCs() {
			try {
				const response = await PooService.getEMCs(this.$route.params)
				this.emcs = [...response.data.emcs]
			} catch (err) {
				this.error = err
			}
		},
		async getEMCsOnSchool() {
			//   
			try {
				const response = await PooService.getEMCsOnSchool(this.$route.params)
				this.emcsOnSchool = [...response.data.emcsOnSchool]
			} catch (err) {
				this.error = err
			}
		},
		async detachEMCFrom(emcOnSchool) {
			try {
				//          ( )
				const response = await PooService.detachFrom(this.$route.params, emcOnSchool.emcId)
				this.emcsOnSchool = [...response.data.emcsOnSchool]
				this.getEMCs()
			} catch (err) {
				this.error = err
			}
		},
	},
}
</script>
<style scoped></style>
