<template>
	<v-row v-if="isSignin && user.UserRole.code == 3" dense>
		<v-col cols="12">
			<v-btn text color="teal accent-4" :to="{ name: 'poo-emc-create' }">   </v-btn>
		</v-col>
		<v-col cols="12">
			<v-card v-if="emcsFilteredForPOO.length === 0" class="mx-auto text-center">
				      
			</v-card>
		</v-col>
		<v-col cols="12">
			<v-card v-for="emc in emcsFilteredForPOO" :key="emc.id">
				<v-card-title class="text-h4"> {{ emc.title }}</v-card-title>
				<v-card-text class="text-h5">
					<div>
						<v-chip
							v-show="emc.isCustom && emc.createdBy === user.id"
							color="red"
							text-color="white"
							pill
						>
							 
						</v-chip>
					</div>
					<p><strong> : </strong> {{ emc.Publisher.name }}</p>
					<p><strong> : </strong> {{ emc.authors }}</p>
					<p><strong> :</strong> {{ emc.grades }}</p>
					<p><strong> :</strong> {{ emc.Level ? emc.Level.name : ' ' }}</p>
					<v-chip v-if="emc.gia == 9" color="indigo lighten-2" text-color="white" pill>
						-{{ emc.gia }}
					</v-chip>
					<v-chip v-if="emc.gia == 11" color="light-blue accent-3" text-color="white" pill>
						-{{ emc.gia }}
					</v-chip>
					<v-chip color="light-blue accent-3" text-color="white" pill>
						{{ emc.Subject.name }}
					</v-chip>
				</v-card-text>
				<v-card-actions>
					<v-btn
						v-if="emc.isCustom && emc.createdBy === user.id"
						text
						color="teal accent-4"
						:to="{ name: 'poo-emc-edit', params: { emcId: emc.id } }"
					>
						
					</v-btn>
					<v-spacer></v-spacer>
					<v-btn
						v-if="emc.isCustom && emc.createdBy === user.id"
						text
						color="red darken-1"
						@click="deleteEMC(emc)"
					>
						
					</v-btn>
				</v-card-actions>
			</v-card>
		</v-col>
	</v-row>
</template>
<script>
import { mapFields } from 'vuex-map-fields'
import PooService from '../../services/pooService'

export default {
	data: () => ({
		error: null,
	}),
	computed: {
		...mapFields(['isSignin', 'user', 'emcs']),
		emcsFilteredForPOO() {
			return this.emcs.filter((emc) => emc.isCustom && emc.createdBy === this.user.id)
		},
	},
	watch: {
		$route() {
			this.getEMCsForConstructor()
		},
	},
	created() {
		this.$store.dispatch('setSubjectsSidebar', true) //  sidebar  EMCs
		this.$store.dispatch('setAreasSidebar', false) //     sidebar EMCsOnSchool  false
		this.getEMCsForConstructor()
	},
	methods: {
		async getEMCsForConstructor() {
			try {
				const response = await PooService.getEMCs(this.$route.params)
				this.$store.dispatch('setEMCs', response.data.emcs)
			} catch (err) {
				this.error = err
			}
		},
		async deleteEMC(emc) {
			try {
				const response = await PooService.deleteEMC(emc)
				if (response.status === 200) this.$store.dispatch('deleteEMC', emc)
			} catch (error) {
				this.error = error
			}
		},
	},
}
</script>
<style></style>
