<template>
	<v-card>
		<v-card-title class="text-h5">   </v-card-title>
		<v-card-text>
			<v-autocomplete
				v-model="model"
				:items="emcsTitles"
				no-data-text=" "
				item-text="Description"
				item-value="emc"
				dense
				rounded
				solo
				hide-selected
				label=""
				placeholder="  ,      "
				prepend-icon="mdi-database-search"
			></v-autocomplete>
		</v-card-text>
		<v-card-actions>
			<v-btn :disabled="!model" color="teal accent-3" @click="attachEMC">
				
				<v-icon right> mdi-check </v-icon>
			</v-btn>
			<v-spacer></v-spacer>
			<v-btn :disabled="!model" color="red lighten-1" @click="model = null">
				
				<v-icon right> mdi-close-circle </v-icon>
			</v-btn>
		</v-card-actions>
		<v-divider></v-divider>
		<v-expand-transition>
			<v-list v-if="model">
				<v-list-item v-for="(field, i) in fields" :key="i">
					<v-list-item-content>
						<v-list-item-title v-text="field.value"></v-list-item-title>
						<v-list-item-subtitle v-text="field.key"></v-list-item-subtitle>
					</v-list-item-content>
					<v-list-item
						><v-text-field
							v-model="additionalDataForEMCOnSchool.studentsCount"
							type="number"
							label=" "
						></v-text-field>
					</v-list-item>
					<v-list-item
						><v-text-field
							v-model="additionalDataForEMCOnSchool.usingCoz"
							label=" "
						></v-text-field
					></v-list-item>
					<v-list-item
						><v-text-field
							v-model="additionalDataForEMCOnSchool.swapCoz"
							label=" "
						></v-text-field
					></v-list-item>
					<v-list-item
						><v-text-field
							v-model="additionalDataForEMCOnSchool.correctionCoz"
							label=" "
						></v-text-field
					></v-list-item>
				</v-list-item>
			</v-list>
		</v-expand-transition>
	</v-card>
</template>
<script>
import { mapFields } from 'vuex-map-fields'
import AdminService from '../../services/adminService'

export default {
	data: () => ({
		model: null,
		additionalDataForEMCOnSchool: {
			swapCoz: null,
			usingCoz: null,
			correctionCoz: null,
			studentsCount: 0,
		},
		descriptionLimit: 200,
		error: null,
	}),
	computed: {
		...mapFields(['emcs', 'emcsOnSchool', 'user']),
		fields() {
			if (!this.model) return []
			return Object.keys(this.model.previewData).map((key) => {
				return {
					key: this.model.previewData[key],
					value: key,
				}
			})
		},
		emcsTitles() {
			return this.emcs.reduce((filtered, entry) => {
				if (
					!!entry &&
					entry.Subject.code === this.$route.params.subjectCode &&
					this.emcsOnSchool.filter(
						(eos) =>
							eos.emcId === entry.id && eos.School.schoolCode === this.$route.params.schoolCode,
					).length === 0
				) {
					const unpreparedDescription =
						entry.title.concat('. ') + entry.authors.concat('. ') + entry.Publisher.name
					const Description =
						unpreparedDescription.length > this.descriptionLimit
							? unpreparedDescription.slice(0, this.descriptionLimit).concat('...')
							: unpreparedDescription

					const emc = []
					emc.previewData = {}
					emc.previewData[''] = entry.title
					emc.previewData[''] = entry.authors
					emc.previewData[''] = entry.Publisher.name
					emc.previewData[''] = entry.Subject.name
					emc.previewData[''] = entry.grades
					emc.previewData[''] = entry.gia
					emc.previewData[''] = entry.isCustom ? '' : ''
					emc.entry = entry
					filtered.push({ Description, emc })
				}
				return filtered
			}, [])
		},
	},
	watch: {
		$route() {
			this.model = null
			this.getEMCs()
		},
	},
	created() {
		this.getEMCs()
	},
	methods: {
		async getEMCs() {
			try {
				console.log('get emcs')

				const response = await AdminService.getEMCs(this.$route.params)

				console.log('new emcs ', response.data.emcs)
				// this.$store.commit('setEMCs', response.data.emcs)
				this.emcs = [...response.data.emcs]
			} catch (err) {
				this.error = err
			}
		},
		async attachEMC() {
			try {
				const response = await AdminService.attachTo(this.$route.params, this.model.entry.id, {
					...this.additionalDataForEMCOnSchool,
				})

				console.log('get response: ', response.data.emcsOnSchool)

				this.emcsOnSchool = response.data.emcsOnSchool
				// this.$emit('onAttachEMCTo', this.model)
				this.model = null
				this.getEMCs()
			} catch (err) {
				this.error = err
			}
		},
	},
}
</script>
<style scope></style>
