<template>
	<v-container class="px-0" fluid>
		<v-card>
			<v-card-title class="text-h4"> : {{ emcOnSchool.EMC.title }} </v-card-title>
			<v-card-text class="text-h5">
				<div>
					<v-chip v-show="emcOnSchool.isApproved" color="green" text-color="white" pill>
						
					</v-chip>
					<v-chip v-show="!emcOnSchool.isApproved" color="yellow" text-color="black" pill>
						 
					</v-chip>
					<v-chip v-show="emcOnSchool.EMC.isCustom" color="red" text-color="white" pill>
						
					</v-chip>
				</div>
				<p><strong> : </strong> {{ emcOnSchool.EMC.Publisher.publisherName }}</p>
				<p><strong> : </strong> {{ emcOnSchool.EMC.authors }}</p>
				<p>: {{ emcOnSchool.EMC.grades }}</p>
				<!--p> - : {{ emcOnSchool.studentsCount }} </p-->
				<p>-{{ emcOnSchool.EMC.gia }}</p>
				<p>: {{ emcOnSchool.EMC.Level ? emcOnSchool.EMC.Level.name : ' ' }}</p>
			</v-card-text>
			<v-card-actions>
				<v-btn text color="teal accent-4" @click="isDetailing = !isDetailing">  </v-btn>
				<v-btn
					text
					color="teal accent-4"
					@click="$emit('onSwapApprovingStatusEMCOnSchool', emcOnSchool)"
				>
					{{ emcOnSchool.isApproved ? ' ' : '' }}
				</v-btn>
				<v-btn
					text
					color="teal accent-4"
					:to="{ name: 'admin-emc-edit', params: { emcId: emcOnSchool.emcId } }"
				>
					
				</v-btn>
				<v-btn text color="teal accent-4" @click="$emit('onDetachEMCFrom', emcOnSchool)">
					 
				</v-btn>
			</v-card-actions>
			<v-expand-transition>
				<v-card v-show="isDetailing">
					<v-card-text class="pb-0">
						<p><strong> :</strong> {{ emcOnSchool.usingCoz }}</p>
						<p><strong> :</strong> {{ emcOnSchool.correctionCoz }}</p>
						<p><strong> : </strong> {{ emcOnSchool.swapCoz }}</p>
					</v-card-text>
				</v-card>
			</v-expand-transition>
		</v-card>
	</v-container>
</template>
<script>
import { mapFields } from 'vuex-map-fields'

export default {
	props: {
		emcOnSchool: {
			type: Object,
			default: null,
		},
	},
	data: () => ({
		isDetailing: false,
	}),
	computed: {
		...mapFields(['isSignin', 'user', 'emcs']),
	},
}
</script>

<style scoped>
.v-card--reveal {
	bottom: 0;
	opacity: 1 !important;
	position: absolute;
	width: 100%;
}
</style>
