<template>
	<v-container v-if="isSignin && user.UserRole.code == 2" class="px-0" fluid>
		<v-card>
			<v-card-title class="text-h4">   </v-card-title>
			<v-card-text class="text-h5">
				<v-text-field
					v-model="emc.title"
					label=""
					placeholder="  "
				></v-text-field>
				<v-text-field
					v-model="emc.authors"
					label=""
					placeholder="  "
				></v-text-field>
				<v-text-field
					v-model="emc.grades"
					label=""
					placeholder="  "
				></v-text-field>
				<v-select
					v-model="emc.Level"
					:items="levels"
					item-text="name"
					no-data-text=" "
					label=""
					solo
					return-object
				></v-select>
				<v-select
					v-model="emc.Publisher"
					:items="publishers"
					item-text="name"
					no-data-text=" "
					label=""
					placeholder="  "
					solo
					return-object
				></v-select>
				<v-select
					v-model="emc.Subject"
					:items="subjects"
					item-text="name"
					no-data-text=" "
					label=""
					placeholder="  "
					solo
					return-object
				></v-select>
			</v-card-text>
			<v-card-actions>
				<v-btn text color="teal accent-4" @click="createEMC">   </v-btn>
				<v-btn text color="red accent-2" :to="{ name: 'pmo-emcs' }">  </v-btn>
			</v-card-actions>
		</v-card>
	</v-container>
</template>
<script>
import { mapFields } from 'vuex-map-fields'
import PmoService from '../../services/pmoService'

export default {
	data: () => ({
		error: null,
		message: null,
		emc: {
			title: null,
			grades: null,
			authors: null,
			levelId: null,
			Level: null,
			subjectId: null,
			Subject: null,
			publisherId: null,
			Publisher: null,
			isCustom: false,
		},
	}),
	computed: {
		...mapFields(['isSignin', 'user', 'publishers', 'subjects', 'levels']),
	},
	created() {
		this.$store.dispatch('setAreasSidebar', false)
		this.$store.dispatch('setSubjectsSidebar', false)
	},
	methods: {
		async createEMC() {
			try {
				console.log('creating emc ', this.emc)
				//   - vuex       
				this.$store.dispatch('createEMC', this.emc)
				await PmoService.createEMC(this.emc)

				this.$router.push({
					name: 'pmo-subject-emcs',
					params: { subjectCode: this.emc.Subject.code },
				})
			} catch (error) {
				this.error = error
			}
		},
	},
}
</script>
<style scope></style>
